import React from 'react'
import "./Appp.css"
import photo1 from "../public/images/max1.jpg"
import photo2 from "../public/images/max2.jpg"
import photo3 from "../public/images/max3.jpg"
import photo4 from "../public/images/max4.jpg"


const Appp = () => {
    return (
        <div className="max">
            <h2>Макс пошол нахуй</h2>
            <div className="photoswrapper">
                <img className="photos" src={photo1} />
                <img className="photos" src={photo2} />
                <img className="photos" src={photo3} />
                <img className="photos" src={photo4} />
            </div>
        </div>
    )
}

export default Appp



